.swiper {
  z-index: 0;

  &--hero {
    width: 100%;
    height: 400px;

    @include mobile {
      height: 302px;
    }

    .swiper-horizontal>.swiper-pagination-bullets,
    .swiper-pagination-bullets.swiper-pagination-horizontal {
      bottom: 16px;

      @include tablet {
        bottom: 19px;
      }

      @include mobile {
        bottom: 98px;
      }
    }

    .swiper-pagination-bullet {
      width: var(--swiper-pagination-bullet-width, var(--swiper-pagination-bullet-size, 14px));
      height: var(--swiper-pagination-bullet-height, var(--swiper-pagination-bullet-size, 14px));

      @include mobile {
        width: var(--swiper-pagination-bullet-width, var(--swiper-pagination-bullet-size, 6px));
        height: var(--swiper-pagination-bullet-height, var(--swiper-pagination-bullet-size, 6px));
      }
    }

    .swiper-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet,
    .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 8px);

      @include mobile {
        margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 4px);
      }
    }

    .swiper-pagination-bullet-active {
      background: $black;
    }
  }


}

.swiper-wrapper {
  z-index: 0;

  &--hero {
    height: 400px;

    @include mobile {
      height: 210px;
    }
  }
}

.swiper-slide-hero {

  height: 100%;

  &--1 {
    background-image: url('../img/hero_1920_1.png');
    @include bg-cover;

    @include tablet {
      background-image: url('../img/hero_1024_1.png');
    }

    @include small-tablet {
      background-image: url('../img/hero_768_1.png');
    }

    @include mobile {
      background-image: url('../img/hero_320_1.png');
    }
  }

  &--2 {
    background-image: url('../img/hero_1920_2.png');
    @include bg-cover;

    @include tablet {
      background-image: url('../img/hero_1024_2.png');
    }

    @include small-tablet {
      background-image: url('../img/hero_768_2.png');
    }

    @include mobile {
      background-image: url('../img/hero_320_2.png');
    }
  }

  &--3 {
    background-image: url('../img/hero_1920_3.png');
    @include bg-cover;

    @include tablet {
      background-image: url('../img/hero_1024_3.png');
    }

    @include small-tablet {
      background-image: url('../img/hero_768_3.png');
    }

    @include mobile {
      background-image: url('../img/hero_320_3.png');
    }
  }

  &--4 {
    background-image: url('../img/hero_1920_4.png');
    @include bg-cover;
  }
}

.swiper-slide-offers {
  width: 23.125% !important;

  @include tablet {
    width: 31.125% !important;
  }

  @include small-tablet {
    width: 47.125% !important;
  }

  @include mobile {
    width: 100% !important;
  }


  &:nth-child(2) {
    width: 48.75% !important;

    @include tablet {
      width: 31.125% !important;
    }

    @include small-tablet {
      width: 47.125% !important;
    }

    @include mobile {
      width: 100% !important;
    }

  }

  &:nth-child(2) .product {

    background-image: url('../img/offers-o_2_1920.png');
    background-position: 92% 92%;
    background-repeat: no-repeat;

    @include tablet {
      background-image: none;
    }

  }

  &:nth-child(2) .product {

    background-image: url('../img/offers-o_2_1920.png');
    background-position: 92% 92%;
    background-repeat: no-repeat;

    @include tablet {
      background-image: none;
    }

    &__image {
      img {
        max-width: 100%;
      }

      @include tablet {
        align-items: center;

        img {
          padding-top: 38px;
        }
      }

      @include mobile {
        img {
          padding-top: 0;
        }
      }
    }

  }

  &:nth-child(2) .product .product__title {
    width: 38%;

    @include tablet {
      width: 85%;
    }
  }

  &:nth-child(5) {
    width: 48.75% !important;

    @include tablet {
      width: 31.125% !important;
    }

    @include small-tablet {
      width: 47.125% !important;
    }

    @include mobile {
      width: 100% !important;
    }
  }

  &:nth-child(5) .product {

    background-image: url('../img/offers-o_5_1920.png');
    background-position: 92% 92%;
    background-repeat: no-repeat;

    @include tablet {
      background-image: none;
    }

    &__image {
      img {
        max-width: 100%;
      }

      @include tablet {
        align-items: center;

        img {
          padding-top: 38px;
        }
      }

      @include mobile {
        img {
          padding-top: 0;
        }
      }
    }
  }

  &:nth-child(5) .product .product__title {
    width: 38%;

    @include tablet {
      width: 85%;
    }
  }
}

.swiper-slide-useful {
  background-color: $grey_light;
  border-radius: 10px;

  @include tablet {
    grid-column: 3 span;
  }
}

.modal-slider {

  padding-top: 22px;
  padding-bottom: 10px;
  padding-left: 50px;
  background-color: $grey_light;

  @include tablet {
    padding-top: 0px;
    padding-bottom: 0px;
    padding-left: 0px;
  }

  &__container {
    overflow: hidden;
  }

  &__slide {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 21% !important;


    img{
      height: 100%;
    }
    @include tablet {
      width: 31% !important;
      justify-content: flex-start;
    }

    @include small-tablet {
      height: 125px;
      width: 50% !important;
      justify-content: flex-start;
    }

    @include mobile {
      height: 86px;
      width: 100% !important;
      justify-content: center;
    }
    .modal-slider__item {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      width: 100%;
      height: 100%;

      @include mobile {
        justify-content: center;
      }
    }

    &:nth-child(1) {
      @include small-tablet {
        width: 55% !important;
      }

      @include mobile {
        width: 100% !important;

      }
    }

    &:nth-child(2) {
      width: 11% !important;

      @include tablet {
        width: 16% !important;

      }

      @include small-tablet {
        width: 30% !important;

      }

      @include mobile {
        width: 100% !important;

      }
    }

    &:nth-child(3) {
      width: 22% !important;

      @include tablet {
        width: 30% !important;

      }

      @include small-tablet {
        width: 40% !important;

      }

      @include mobile {
        width: 100% !important;
        img{
          height: 75%;
          width: 80%;
        }
      }
    }

    &:nth-child(4) {
      @include tablet {
        width: 30% !important;

      }

      @include small-tablet {
        width: 40% !important;

      }

      @include mobile {
        width: 100% !important;
        img{
          height: 66%;
          width: 76%;
        }
        }
      }
    }
}

.swiper-slide-cart {
  width: 296px;
}

.useful-slider {

  &__slide {}

}
