.basket-modal {
  border-radius: 10px;
  padding: 30px;
  width: 624px;

  @include mobile {
    width: 92.5%;
    padding: 30px 18px;
    padding-right: 18px;
  }

  &__title {
    margin-bottom: 8px;

    @include mobile {
      width: 100%;
    }
  }

  &__descr {
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 19px;
    color: $black;
    max-width: 374px;
    margin-bottom: 30px;

    @include mobile {
      margin-bottom: 15px;
      padding-right: 5px;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @include mobile {}
  }

  &__label {
    width: 100%;
    display: flex;
    flex-direction: column-reverse;
  }

  &__btn {
    margin-bottom: 12px;
    padding: 18px 33px;
  }


}

.send-modal {
  border-radius: 10px;
  padding: 118px 75px;
  width: 624px;


  @include mobile {
    width: 92.5%;
    padding: 60px 51px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__descr {
    font-family: 'Open Sans';
    font-size: 32px;
    line-height: 100%;
    color: $black;

    @include mobile {
      font-size: 16px;
    }
  }

}

.basket-modal-order {
  padding: 30px;
  border: 1px solid $grey;
  border-radius: 10px;
  margin-bottom: 20px;

  &__content {
    position: relative;
  }

  &__top {
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 19px;
    color: $grey;
    max-width: 374px;
    margin-bottom: 30px;

    @include mobile {
      font-size: 12px;
      margin-bottom: 15px;
    }
  }

  &__bold {
    font-family: 'Open Sans Bold';
    color: $black;
  }

  &__show {
    cursor: pointer;
    margin-bottom: 20px;

    svg {
      width: 8px;
      height: 8px;
      transform: rotate(180deg);
      fill: currentColor;
      transition: transform 0.4s ease-in-out;
    }

    &--active {
      svg {
        transform: rotate(0deg);
      }
    }
  }

  &__list {
    display: none;

    &--visible {
      display: block;
    }

    .basket-product {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 12px 20px;
      transition: bacground-color 0.3s ease-in-out;


      @include for-desktop {
        &:hover {
          background-color: $md_grey;
        }
      }

      &__img {
        max-width: 100px;
        max-height: 100px;
        object-fit: cover;

        @include mobile {
          max-width: 50px;
          max-height: 50px;
        }

      }

      &__text {}

      &__title {
        max-width: 280px;
        margin-bottom: 10px;
        font-family: 'Open Sans';
        font-size: 20px;
        line-height: 130%;
        color: $black;

        @include mobile {
          max-width: 135px;
          font-size: 14px;
          line-height: 120%;
        }
      }

      &__price {
        font-family: 'Open Sans';
        font-size: 16px;
        line-height: 130%;
        color: $grey;

        @include mobile {
          font-size: 12px;
          line-height: 120%;
        }
      }

      &__delete {
        width: 40px;
        height: 40px;
        border: none;
        background: transparent;
        padding: 0;
        background-image: url('../img/basket.svg');
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        cursor: pointer;

        @include mobile {
          width: 30px;
          height: 30px;
        }
      }
    }

    .simplebar-offset {
      top: 30px;
    }
  }
}

.scale-modal {

  width: 67%;
  padding: 0;
  border-radius: 10px;

  @include tablet {
    width: 93%;
  }

  &__prev {
    @include tablet {
      position: absolute;
      top: 41%;
      left: 14px;
    }

    @include mobile {
      top: 36%;
    }
  }

  &__next {
    @include tablet {
      position: absolute;
      top: 41%;
      right: 18px;
    }

    @include mobile {
      top: 36%;
    }
  }


  @include small-tablet {
    width: 81%;
  }

  @include mobile {
    width: 92.5%;
  }

  &__image {
    padding-top: 50px;
    margin-bottom: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      width: 83%;
    }
    @include tablet {
      img {
        width: 86%;
      }

    }

    @include small-tablet {
      img {
        width: 93.6%;
      }
    }

    @include mobile {
      padding-top: 32px;
      margin-bottom: 33px;

      img {
        width: 89%;
      }
    }
  }

  &__slaider {
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    position: relative;

    @include tablet {
      padding: 20px 66px;
      position: relative;
      align-items: center;
      background: $grey_light;
    }

    @include small-tablet {
      padding: 20px 77px;
    }

    @include mobile {
      padding: 16px 62px;
    }
  }
}



.cart-image {
  margin-bottom: 0px;


  &__bottom {
    height: 166px;
    justify-content: space-around;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;

    @include tablet {
      display: none;
    }
  }
}

.graph-modal {
  &__close {
    position: absolute;
    right: 19px;
    top: 19px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    padding: 0;
    width: 24px;
    height: 24px;
    background-color: $white;
    background-image: none;
    cursor: pointer;

    svg {
      width: 20px;
      height: 20px;
      transition: fill 0.4s ease-in-out, background-color 0.4s ease-in-out;
    }

    @include for-desktop {
      &:focus-visible {
        svg path {
          fill: $primary;
        }
      }

      &:hover:not(:focus-visible) {
        background-color: $pink_violet;

        svg path {
          fill: $primary_shade;
        }
      }
    }

    &:active:not(:focus-visible) {
      svg path {
        fill: $primary_shade;
      }
    }

    @include mobile {
      right: 16px;
      top: 16px;
      width: 12px;
      height: 12px;
    }
  }
}

.modal-slider {

  &__prev {
    @include tablet {
      position: absolute;
      top: 42%;
      left: 16px;
    }

    @include mobile {
      top: 37%;
    }
  }

  &__next {
    @include tablet {
      position: absolute;
      top: 42%;
      right: 3px;
    }

    @include mobile {
      top: 37%;
    }
  }

}
