.rating {
  padding-top: 69px;
  padding-bottom: 70px;

  &__list {
    @include tablet {
      gap: 32px;
    }

    @include mobile {
      gap: 15px;
    }
  }

  &__list-item {
    grid-column: 3 span;

  }



  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__btn {
    margin-top: 35px;
    padding: 22px 35px;

    @include mobile {
      margin-top: 31px;
    }
  }
}
