/* stylelint-disable */

.custom-checkbox {
  position: relative;

  &__1 {
    display: inline-block;

    @include mobile {
      display: none;
    }
  }

  &__2 {
    display: none;

    @include mobile {
      display: inline-block;
    }
  }

  &__field {
    appearance: none;
    position: absolute;
  }

  &__content {
    padding-left: 22px;
    cursor: pointer;
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 19px;
    color: $black;

    @include mobile {
      font-size: 12px;
      line-height: 16px;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      border: 1px solid $grey;
      border-radius: 2px;
      width: 14px;
      height: 14px;
      box-sizing: border-box;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 14px;
      height: 14px;
      box-sizing: border-box;
      background-image: url('../img/checkbox.svg');
      background-position: center;
      background-size: 10px 10px;
      background-repeat: no-repeat;
      background-color: $primary;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }
  }

  &__category {
    padding-left: 31px;
    cursor: pointer;
    font-family: 'Open Sans';
    font-size: 16px;
    line-height: 130%;
    color: $grey_shade;


    @include mobile {
      padding-left: 26px;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      border: 1px solid $grey;
      border-radius: 2px;
      width: 20px;
      height: 20px;
      box-sizing: border-box;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 20px;
      height: 20px;
      box-sizing: border-box;
      border-radius: 2px;
      background-image: url('../img/checkbox.svg');
      background-position: center;
      background-size: 14px 14px;
      background-repeat: no-repeat;
      background-color: $primary;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }
  }
}

.custom-checkbox__field:checked+.custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus+.custom-checkbox__content::before {
  border-color: $primary;
}

.custom-checkbox__field:disabled+.custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

.custom-checkbox__field:checked+.custom-checkbox__category::after {
  opacity: 1;
}

.custom-checkbox__field:focus+.custom-checkbox__category::before {
  border-color: $primary;
}

.custom-checkbox__field:disabled+.custom-checkbox__category {
  opacity: 0.4;
  pointer-events: none;
}
