option:checked {
  display: none;
}

.select {
  &--city {
    .choices {
      &__inner {
        border: none;
        font-family: 'Open Sans';
        font-size: 16px;
        line-height: 120%;
        color: $primary;
        padding-left: 0;
      }

      &__item {
        &--choice {
          font-family: 'Open Sans';
          font-size: 14px;
          line-height: 130%;
          color: $black;
        }
      }


    }

    .choices[data-type*=select-one] .choices__inner {
      background: none;
      transition: color 0.4s ease-in-out, background-color 0.4s ease-in-out;

    }

    .choices__list--dropdown .choices__item--selectable,
    .choices__list[aria-expanded] .choices__item--selectable {
      padding-right: 0;
    }

    .choices__list--dropdown,
    .choices__list[aria-expanded] {
      z-index: 2;
      min-width: 84px;
      max-height: 114px;
      background-color: $white;
      border: 2px solid $wild_sand;
      word-break: normal;
    }

    .choices[data-type*=select-one] {
      &::after {
        content: '';
        position: absolute;
        right: 10px;
        top: 15px;
        width: 8px;
        height: 8px;
        border: none;
        transform: rotate(-45deg);
        pointer-events: none;
        border-style: solid;
        border-width: 0 0 1px 1px;
        border-color: $primary;
        margin-top: 0;
        pointer-events: none;
        transition: border-color 0.4s ease-in-out, transform 0.4s ease-in-out;
      }
      @include for-desktop {
        &:hover:not(:focus-visible) {
          &::after{
            border-color: $primary_light;
          }
          .choices__inner{
            color: $primary_light;
          }
        }
        &:focus-visible {
          .choices__inner{
            color: $primary_shade;
            background-color: $pink_light;
          }
        }
        &:active:not(:focus-visible) {
          .choices__inner{
            color: $primary_shade;
          }

        }
      }

    }

    .choices[data-type*=select-one].is-open {
      &::after {

        transform: rotate(135deg);
        border-color: $primary;
        right: 10px;
        top: 20px;
        margin-top: 0;
      }
    }
  }

  &--category {
    width: 28%;
    height: 100%;

    transition: border-color 0.4s ease-in-out, background-color 0.4s ease-in-out;

    @include for-desktop {
      &:hover:not(:focus-visible) {
        border-color: $primary;


      }

      &:focus-visible {
        border-color: $primary;
        background-color: $pink_light;

      }

    }

    &:active:not(:focus-visible) {
      border-color: $primary_shade;
    }

    @include tablet {
      width: 32%;
    }

    @include mobile {
      height: 60px;
      width: 100%;
    }

    .choices {

      &__inner {
        align-items: center;
        border: none;
        font-family: 'Open Sans';
        font-size: 16px;
        line-height: 120%;
        color: $primary;
        padding-left: 0;
        border: 2px solid $wild_sand;
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
      }
    }


    .choices[data-type*=select-one] {
      transition: border-color 0.4s ease-in-out, background-color 0.4s ease-in-out;
      .choices__inner{
        transition: border-color 0.4s ease-in-out, background-color 0.4s ease-in-out;
      }
      @include for-desktop {
        &:focus-visible {
          .choices__inner {
            border-color: $primary;
            background-color: $pink_light;
          }

        }

        &:hover:not(:focus-visible) {
          .choices__inner {
            border-color: $primary;
          }

        }

      }

      &:active:not(:focus-visible) {
        .choices__inner {
          border-color: $primary_shade;
        }

      }

    }

    .is-flipped.is-open {
      transition: border-color 0.4s ease-in-out;
      @include for-desktop {
        &:hover:not(:focus-visible) {
          .choices__inner {
            border-top-right-radius: 10px;
            border-bottom-right-radius: 10px;
            border-color: $wild_sand;
          }

        }
      }

    }

    .choices[data-type*=select-one] .choices__inner {
      background: transparent;
      padding-top: 12px;
      padding-bottom: 16px;
      height: 100%;

      @include tablet {
        padding-top: 16px;
        padding-bottom: 13px;
      }

      @include mobile {
        border-radius: 10px;
      }

    }

    .choices__list {
      &--single {
        padding-left: 29px;
      }
    }

    .choices[data-type*=select-one] {

      &::after {
        content: '';
        position: absolute;
        right: 32px;
        top: 22px;
        width: 8px;
        height: 8px;
        border: none;
        transform: rotate(-45deg);
        pointer-events: none;
        border-style: solid;
        border-width: 0 0 1px 1px;
        border-color: $primary;
        margin-top: 0;
        pointer-events: none;
        transition: all 0.4s ease-in-out;

        @include tablet {
          right: 34px;
          top: 25px;
        }
      }
    }

    .choices[data-type*=select-one].is-open {
      &::after {

        transform: rotate(135deg);
        border-color: $primary;
        right: 32px;
        top: 25px;
        margin-top: 0;

        @include tablet {
          right: 34px;
          top: 28px;
        }
      }
    }

    .choices__list[aria-expanded] {
      top: 60px;
      width: 100%;
      height: 194px;
      background-color: $white;
      word-break: normal;
      z-index: 100;

      @include small-tablet {
        height: 250px;
      }

      @include mobile {
        height: 250px;
      }
    }


    .is-flipped .choices__list[aria-expanded] {
      top: 60px;
      bottom: -100%;
    }

    .is-open .choices__list[aria-expanded] {
      border-left: 2px solid $wild_sand;
      border-right: 2px solid $wild_sand;
      border-bottom: 2px solid $wild_sand;
      border-top: none;
      border-bottom-left-radius: 10px;
      border-bottom-right-radius: 10px;

    }

    &--item {
      font-family: 'Open Sans';
      font-size: 16px;
      line-height: 130%;
      color: $black;
    }

    .choices__list[aria-expanded] .choices__list {
      overflow: hidden;
      padding-top: 22px;
      padding-left: 30px;
      padding-right: 79px;
      display: grid;
      grid-template-columns: 70px 91px;
      grid-template-rows: repeat(5, 22px);
      gap: 10px 30px;

      @include small-tablet {
        grid-template-columns: 91px;
        grid-template-rows: repeat(9, 22px);
        gap: 10px;
      }
    }

    .choices__list[aria-expanded] .choices__item {
      padding: 0;
      font-family: 'Open Sans';
      font-size: 16px;
      line-height: 130%;
      color: $black;

    }
  }
}




.is-selected {
  display: none
}

