.breadcrumbs {
  padding-top: 20px;
  padding-bottom: 32px;

  @include tablet {
    padding-top: 19px;
    padding-bottom: 28px;
  }

  &--cooperation {
    padding-bottom: 30px;
  }

  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-right: 13px;

      &::after {
        content: "/";
        position: absolute;
        right: -10px;
        top: 50%;
        font-size: 14px;
        color: $grey;
        transform: translateY(-50%);
      }
    }


  }

  &__link {
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 100%;
    color: $grey;

    &--current {
      color: $primary_shade;
    }
  }
}

.breadcrumbs-cart {
  padding-bottom: 27px;

  @include mobile {
    display: none;
  }
}

.breadcrumbs-catalog {
  @include mobile {
    display: none;
  }
}

.breadcrumbs-cooperation {
  @include mobile {
    display: none;
  }
}

.breadcrumbs-contact {
  @include mobile {
    display: none;
  }
}
