@import 'vars';

.footer {
  &__container {
    @include small-tablet {
      gap: 3px;
    }
  }

  &__top {
    padding-top: 12px;

    background: $md_grey;
    align-items: center;

    @include tablet {
      padding-top: 12px;
    }

    @include small-tablet {
      padding-top: 13px;
    }

    @include mobile {
      padding-top: 11px;
      padding-bottom: 0px;

    }

    &--left {
      grid-column: 4 span;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;

      @include tablet {
        grid-column: 2 span;
      }

      @include small-tablet {
        grid-column: 6 span;
        flex-direction: row;
        justify-content: space-between;
      }

      @include mobile {
        flex-direction: column;
        align-items: center;
        margin-bottom: 14px;
      }
    }

    &--right {
      grid-column: 8 span;
      display: flex;
      justify-content: flex-end;

      @include tablet {
        grid-column: 7 span;
      }

      @include mobile {
        padding-top: 0;
      }
    }
  }

  &__bottom {
    padding-top: 5px;
    padding-bottom: 6px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__bottom-descr {
    font-family: 'Open Sans';
    font-size: 15px;
    line-height: 19px;
    color: $grey;
  }

  &__logo {
    display: inline-block;
    margin-bottom: 21px;

    img {
      width: 100%;
    }

    @include small-tablet {
      margin-bottom: 0px;
    }

    @include mobile {
      margin-bottom: 26px;
    }
  }



  &__list {

    width: 100%;
    display: flex;
    justify-content: space-between;

    @include mobile {
      flex-wrap: wrap;
    }

  }

  &__list-item {
    display: flex;
    flex-direction: column;

    @include mobile {
      width: 46%;
      margin-right: 5px;
      margin-bottom: 24px;
    }
    &:nth-child(1) {
      transform: translate(0px, 2px);

      @include small-tablet {
        transform: translate(0, 0);
      }

    }

    &:nth-child(2) {
      transform: translate(-16px, 2px);

      @include small-tablet {
        transform: translate(-16px, 0px);
      }

      @include mobile {
        transform: translate(0, 0);
      }
    }

    &:nth-child(3) {
      transform: translate(-16px, 2px);

      @include small-tablet {
        transform: translate(-16px, 0px);
      }

      @include mobile {
        transform: translate(0, 0);
      }
    }
    &:nth-child(4) {
      transform: translate( 0px, 2px);

      @include small-tablet {
        transform: translate(0, 0);
      }
    }
    &:not(:last-child) {
      margin-right: 10px;

      @include mobile {
        margin-right: 4px;
      }
    }

  }

  &__list-item:nth-child(3),
  &__list-item:nth-child(4) {
    margin-bottom: 22px;
  }

  &__title {
    font-family: 'Open Sans';
    font-size: 16px;
    line-height: 100%;
    color: $black;
    margin-bottom: 5px;
  }

  &__prop {
    display: flex;
    flex-direction: column;
  }

  &__prop-item {
    &:not(:last-child) {
      margin-bottom: 6px;
    }
  }

  &__prop-link {
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 100%;
    color: $grey;

    &--ef {
      position: relative;
      background-color: transparent;
      border: 0;
      z-index: 1;
      cursor: pointer;
      transition:color 0.4s ease-in-out;

      &::before {
        content: "";
        position: absolute;
        top: -2px;
        bottom: -2px;
        left: -5px;
        right: -5px;
        background-color: $pink_violet;
        z-index: -1;

        opacity: 0;
        transition: opacity 0.4s ease-in-out;
      }

      @include for-desktop {
        &:focus-visible::before {
          opacity: 1;
        }

        &:hover:not(:focus-visible) {
          color: $primary;
        }

      }

      &:active:not(:focus-visible) {
        color: $primary-shade;
      }
    }
  }
}
