@import 'vars';
@import 'fonts';

.nav {

  grid-column: 9 span;

  @include tablet {
    grid-column: 7 span;
    display: flex;
    justify-content: flex-end;
  }

  @include small-tablet {
    width: 100%;
    height: 300px;
    position: absolute;
    top: 15px;
    left: -72px;
    justify-content: flex-start;
    z-index: 99;
    background-color: $white;
    transform: translateX(-100%);
    transition: transform 0.4s ease-in-out;
    background-image: url('../img/nav-image.png');
    background-position: 88% 68%;
    background-repeat: no-repeat;

    &--active {
      transform: none;
      left: 0;
    }
  }

  @include mobile {
    width: 100%;
    height: 533px;
    position: absolute;
    top: 130px;
    left: 0;
    z-index: 99;
    background-color: $white;
    transform: translateX(-100%);
    transition: transform 0.4s ease-in-out;
    background-image: none;

    &--active {
      transform: none
    }
  }

  &__list {
    @include flex-v-center;
    justify-content: space-between;

    @include tablet {
      width: 96%;
      justify-content: space-between;
    }

    @include small-tablet {
      width: 56%;
      margin-top: 91px;
      padding-left: 72px;
      height: 200px;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      justify-content: flex-start;
      align-items: flex-start;
    }

    @include mobile {
      margin-top: 70px;
      padding-left: 14px;
      width: 56%;
      height: 200px;
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      align-items: flex-start;
    }
  }

  &__item {
    @include small-tablet {
      &:not(:last-child) {

        margin-bottom: 41px;

      }
    }
  }

  &__link {
    position: relative;
    cursor: pointer;
    font-family: 'Open Sans';
    font-size: 16px;
    line-height: 22px;
    color: $black;

    &--ef {
      background-color: transparent;
      border: 0;
      z-index: 1;
      cursor: pointer;
      transition:color 0.4s ease-in-out;

      &::before {
        content: "";
        position: absolute;
        top: -2px;
        bottom: -2px;
        left: -5px;
        right: -5px;
        background-color: $pink_violet;
        z-index: -1;

        opacity: 0;
        transition: opacity .4s ease-in-out;
      }

      @include for-desktop {
        &:focus-visible::before {
          opacity: 1;
        }

        &:hover:not(:focus-visible) {
          color: $primary;
        }
      }

      &:active:not(:focus-visible) {
        color: $primary-shade;
      }
    }
  }


}
