.catalog-filters {
  grid-column: 3 span;

  @include tablet {
    grid-column: 9 span;

  }
}

.catalog-filter {
  @include tablet {
    display: flex;
    justify-content: space-between;
  }

  @include small-tablet {
    padding-top: 10px;
  }

  &__top {
    display: flex;
    flex-direction: column;
    gap: 18px;
    margin-bottom: 60px;

    @include tablet {
      width: 100%;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 0;
    }

    @include small-tablet {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__category {
    margin-bottom: 30px;

    @include tablet {
      margin-bottom: 0;

      &:not(:last-child) {
        margin-right: 32px;
      }
    }

    @include small-tablet {
      margin-bottom: 15px;

      &:not(:last-child) {
        margin-right: 32px;
      }
    }

    @include mobile {
      &:not(:last-child) {
        margin-right: 0px;
      }

      &:nth-child(1) {
        margin-right: 15px;
      }

      &:nth-child(3) {
        margin-right: 15px;
      }
    }
  }

  &__title {
    margin-top: 0;

    font-family: 'Open Sans';
    font-size: 18px;
    line-height: 130%;
    color: $black;

    &--2 {
      display: none;
    }

    @include tablet {
      grid-column: 1 span;

      &--1 {
        display: none;
      }

      &--2 {
        display: block;
        margin-bottom: 0;
        font-size: 14px;
        line-height: 130%;
      }
    }
  }



  &__item {
    &:not(:last-child) {
      margin-bottom: 11px;
    }
  }

}

.catalog-category {
  &__title {
    margin-top: 0;
    margin-bottom: 7px;
    font-family: 'Open Sans Semibold';
    font-size: 16px;
    line-height: 130%;
    color: $grey_shade;

    @include tablet {
      margin-bottom: 0;
    }
  }
}

.catalog-price {
  margin-bottom: 38px;

  @include tablet {
    margin-bottom: 0;
  }

  &__items {
    width: 100%;
    display: flex;
    flex-direction: column;

  }

  &__prop {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 8px;

    @include tablet {
      width: 114px;
      flex-direction: column;
      margin-bottom: 0;
    }
  }

  &__item {
    display: flex;
    align-items: center;

    @include tablet {
      &:not(:last-child) {
        margin-bottom: 11px;
      }
    }
  }

  &__descr {
    margin-right: 9px;
    font-family: 'Open Sans';
    font-size: 16px;
    line-height: 130%;
    color: $grey_shade;

  }

  &__input {
    font-family: 'Open Sans';
    font-size: 16px;
    line-height: 130%;
    color: $grey_shade;
    padding-left: 13px;
    width: 86px;
    height: 30px;
    border: 1px solid $grey;
    border-radius: 2px;
    transition: border-color 0.4s ease-in-out, background-color 0.4s ease-in-out ;

    &::placeholder {
      color: $grey_shade;
    }



    @include for-desktop {
      &:focus-visible {
        border-color: $primary;
      }

      &:hover:not(:focus-visible) {
        border-color: $primary;
        background-color: $pink_violet;
      }

    }

    &:active:not(:focus-visible) {
      border-color: $primary_shade;
    }
  }

  &__prop {}

  &__element {
    @include tablet {
      display: none;
    }
  }
}

.catalog-collection {

  @include tablet {
    display: none;
  }

  &__cart {
    padding: 0;
    background-color: $black;
  }

  &__wrapper {
    padding: 28px 30px;
  }

  &__title {
    font-family: 'Playfair Display';
    font-size: 24px;
    line-height: 32px;
    color: $gold;
  }

  &__descr {
    margin-bottom: 12px;
    width: 70%;
    font-family: 'Open Sans';
    font-size: 16px;
    line-height: 22px;
    color: $grey;
  }
}

.catalog-list-open {
  @include tablet {

    position: absolute;
    padding-top: 22px;
    padding-left: 30px;
    padding-bottom: 30px;
    width: 187px;
    background-color: $white;
    border: 1px solid $md_grey;
    border-radius: 0px 0px 10px 10px;
    z-index: 5;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.4s ease-in-out, visibility 0.4s ease-in-out;

    &--active {
      opacity: 1;
      visibility: visible;
    }
  }

  @include mobile {
    width: 140px;
    padding-left: 15px;

  }
}


.btn-catalog {
  @include tablet {
    padding: 9px 15px;
    width: 187px;

    background: $grey_light;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    transition: background-color 0.4s ease-in-out, border 0.4s ease-in-out;

    &--active {
      background-color: $white;
      border: 2px solid $primary_shade;
      border-radius: 10px;

      .catalog-icon {
        transform: rotate(180deg);
      }
    }
  }

  @include mobile {
    width: 140px;
  }
}

.catalog-icon {
  display: none;

  @include tablet {
    display: inline-block;
    transition: all 0.4s ease-in-out;

    &--active {}
  }
}
