.cart-bottom {
  padding-bottom: 70px;

  @include tablet {
    padding-bottom: 72px;
  }

  @include mobile {
    padding-bottom: 116px;
  }

  &__container {
    position: relative;
  }

  &__title {
    margin-bottom: 24px;
    font-family: 'Open Sans';
    font-size: 24px;
    line-height: 130%;
    color: $black;
  }
}
