.callback {
  &__container {
    padding-top: 70px;
    padding-bottom: 70px;
    background-image: url('../img/callback.png');
    background-position: 97% 50%;
    background-repeat: no-repeat;

    @include tablet {
      background-image: url('../img/callback_1024.png');
      background-position: 93% 50%;
      padding-bottom: 67px;
    }

    @include small-tablet {
      background-image: url('../img/callback_768.png');
      background-position: 88% 50%;
      background-size: 24.2%;
    }

    @include mobile {
      padding-bottom: 71px;
      background-image: none;
    }
  }

  &__cart {
    grid-column: 6 span;

    @include tablet {
      grid-column: 4 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__title {
    margin-bottom: 9px;

    @include tablet {
      width: 100%;
    }

    @include mobile {
      margin-bottom: 6px;
    }
  }

  &__descr {
    margin-bottom: 16px;
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 19px;
    color: $grey_shade;

  }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__label {
    width: 100%;
    display: flex;
    flex-direction: column-reverse;
  }

  &__btn {
    margin-bottom: 14px;
    padding: 19px 33px;

    @include mobile {
      margin-bottom: 10px;
    }
  }

  &__link {
    color: $primary;
    text-decoration: underline;
  }
}
