.useful {
  @include mobile {
    padding-bottom: 48px;

    .swiper-btns {
      right: 34%;
      top: 103%;
    }
  }

  &__container {
    position: relative;
  }

  &__cart {
    width: 100%;
    @include tablet {}
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    position: relative;
    min-height: 189px;
    padding: 25px 30px;
    padding-bottom: 30px;

    @include tablet {
      min-height: 194px;
    }
  }

  &__btn {
    display: inline-block;
  }

  &__title {

    font-family: 'Open Sans';
    font-size: 24px;
    line-height: 33px;
    color: $black;

    @include tablet {
      font-size: 16px;
      line-height: 22px;
    }
  }

  &__image {
    width: 100%;

    @include tablet {
      width: 100%;
    }
  }
}


