.product {
  position: relative;

  background: $grey_light;
  border-radius: 10px;
  padding: 30px;



  &--rating {
    padding: 30px 15px;

    @include tablet {
      img {
        width: 100%;
      }
    }

    @include mobile {
      padding-top: 31px;
      padding-bottom: 12px;
    }
  }

  &--catalog {
    @include mobile {
      padding-bottom: 13px;
    }
  }

  &--cart {
    @include mobile {}
  }

  &__rating {
    position: absolute;
    top: 30px;
    left: 30px;
    display: inline-flex;
    align-items: center;
    font-family: 'Open Sans';
    font-size: 16px;
    line-height: 130%;
    color: $secondary;

    svg {
      fill: currentColor;
      margin-right: 7px;

      @include mobile {
        width: 13px;
        height: 12px;
        margin-right: 5px;
      }
    }

    @include mobile {
      top: 16px;
      left: 16px;
      font-size: 12px;
    }
  }

  &__discont {
    background-color: $secondary;
    width: 82px;
    height: 34px;
    position: absolute;
    top: 32px;
    left: 0;
    font-family: 'Open Sans Bold';
    font-size: 14px;
    line-height: 19px;
    color: $white;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;

    @include tablet {
      top: 28px;
    }

    &::after {
      content: '';
      position: absolute;
      right: -27px;
      top: 0;
      border: 17px solid transparent;
      border-left: 11px solid $secondary;
    }
  }

  &__icon {
    position: absolute;
    right: 30px;
    bottom: 30px;
  }


  &__image {
    display: flex;
    min-height: 245px;
    justify-content: center;
    align-items: flex-end;
    margin-bottom: 16px;
    padding-top: 10px;

    @include tablet {
      padding-top: 7px;
    }

    @include mobile {
      padding-top: 5px;
    }

    img {

      display: block;
      object-fit: contain;
    }

  }

  &__image-rating {
    display: flex;
    min-height: 163px;
    justify-content: center;
    align-items: flex-end;
    margin-bottom: 16px;

    img {
      display: block;
      object-fit: contain;
      width: 100%;

      @include mobile {
        width: 108px;
        min-height: 33px;
      }
    }

    @include tablet {
      height: 164px;
    }

    @include mobile {
      min-height: 59px;
      height: 59px;
      margin-bottom: 19px;
    }
  }

  &__image-catalog {
    display: flex;
    height: 162px;
    justify-content: center;
    align-items: flex-end;
    margin-bottom: 16px;
    padding-top: 10px;

    @include mobile {
      height: 58px;
      padding-top: 16px;
      margin-bottom: 19px;

      img {
        height: 100%;
      }
    }

    img {

      display: block;
      object-fit: contain;
    }

  }

  &__image-cart {
    display: flex;
    height: 162px;
    justify-content: center;
    align-items: flex-end;
    margin-bottom: 16px;

    img {

      display: block;
      object-fit: contain;
    }

    @include tablet {
      img {
        width: 88%;
      }
    }

    @include mobile {
      margin-bottom: 6px;
      height: 70px;
      align-items: center;

      img {
        width: 100%;
      }
    }
  }


  &__title {
    width: 100%;
    margin-bottom: 21px;
    font-family: 'Open Sans';
    font-size: 24px;
    line-height: 130%;
    color: $black;

    &--sm {
      width: 35%;
    }

    &--rating {
      padding-left: 15px;

      @include mobile {
        width: 100%;
        font-size: 16px;
        line-height: 130%;
        padding-left: 0;
        margin-bottom: 16px;
      }
    }

    &--catalog {
      padding-left: 15px;

      @include mobile {
        font-size: 16px;
        line-height: 130%;
        padding-left: 0;
        margin-bottom: 19px;
      }
    }

    &--cart {
      width: 100%;
      padding-left: 15px;

      @include mobile {
        padding-left: 0;
        margin-bottom: 16px;
        font-size: 16px;
        line-height: 130%;
      }
    }
  }

  &__price {
    &--rating {
      padding-left: 15px;

      @include mobile {
        font-size: 16px;
        line-height: 130%;
      }
    }

    &--cart {
      padding-left: 15px;

      @include mobile {
        padding-left: 0;
        margin-bottom: 14px;
      }
    }


  }


  &__btn {
    padding: 15px 33px;
    background: transparent;
    border: 2px solid $primary;
    border-radius: 10px;
    color: $primary;
    cursor: pointer;
    transition: color 0.4s ease-in-out, background-color 0.4s ease-in-out, border-color 0.4s ease-in-out;

    @include tablet {
      padding: 16px 32px;
    }


    &--rating {
      transform: translateX(15px);

      @include mobile {
        padding: 10px 22px;
        transform: translateX(0);
      }
    }

    &--catalog {
      padding: 17px 31px;

      @include mobile {
        padding: 12px 22px;
        font-size: 16px;
        line-height: 100%;
        transform: translateX(0px);
      }
    }

    &--disabled {
      opacity: 0.4;
      pointer-events: none;
    }

    &--ef {
      @include for-desktop {
        &:hover:not(:focus-visible) {
          background-color: $primary;
          color: $white;

        }

        &:focus-visible {
          background-color: $primary_light;
          color: $white;
        }
      }

      &:active:not(:focus-visible) {
        background-color: $primary_shade;
        border-color: $primary_shade;
        color: $white;
      }

    }
  }

}

.product-price {
  display: flex;
  align-items: center;
  padding-left: 15px;
  margin-bottom: 25px;

  @include tablet {
    margin-bottom: 23px;

  }
  @include small-tablet{
    padding-left: 0;
  }
  @include mobile{
    margin-bottom: 26px;
  }

  &--catalog {
    @include mobile {
      margin-bottom: 12px;
    }

  }

  &--rating {
    @include small-tablet {
      padding-left: 15px;
    }

    @include mobile {
      padding-left: 0;
      margin-bottom: 19px;

    }
  }

  &__current {
    margin-right: 30px;
    font-family: 'Open Sans';
    font-size: 18px;
    line-height: 130%;
    color: $secondary;

  }

  &__old {
    font-family: 'Open Sans';
    font-size: 16px;
    line-height: 130%;
    color: $grey;
    text-decoration: line-through;
  }


  &__rating {
    font-family: 'Open Sans';
    font-size: 16px;
    line-height: 130%;
    color: $grey;

    @include mobile {
      margin-right: 0;
    }
  }

  &__cart {
    @include tablet {
      padding-left: 15px;
    }

    @include mobile {
      padding-left: 0;
      margin-bottom: 13px;
    }
  }


}

.rating {
  &__list-item {
    grid-column: 3 span;
  }
}
