.cooperation {
  padding-bottom: 70px;

  @include tablet {
    padding-bottom: 68px;
  }

  @include small-tablet {
    padding-bottom: 66px;
  }

  @include mobile {
    padding-bottom: 73px;

  }

  &__container {
    @include small-tablet {
      padding-right: 60px;
    }

    @include mobile {
      padding-top: 30px;
      padding-right: 5px;
    }
  }

  &__content {
    grid-column: 8 span;

    @include small-tablet {
      grid-column: 6 span;
    }
  }

  &__title {
    margin-bottom: 13px;
    font-family: 'Open Sans';
    font-size: 24px;
    line-height: 33px;
    color: $black;

    @include tablet {
      margin-bottom: 12px;
    }

    @include mobile {
      font-size: 16px;
      line-height: 22px;
    }

    &--sm {
      @include mobile {
        font-size: 14px;
        line-height: 19px;
      }
    }
  }

  &__item {
    margin-bottom: 32px;

    @include mobile {
      margin-bottom: 30px;
    }
  }

  &__descr {
    font-family: 'Open Sans';
    font-size: 16px;
    line-height: 180%;
    color: $grey_shade;

    @include small-tablet {
      line-height: 28.8px;
    }

    @include mobile {
      font-size: 14px;
      line-height: 180%;
    }
  }

}

.cooperation-get {
  width: 74%;

  @include small-tablet {
    width: 98%;
  }

  &__item {

    display: flex;

    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &__image {
    margin-right: 12px;
  }

  &__descr {
    line-height: 150%;
  }
}

.benefits {
  &__title {
    margin-bottom: 12px;
    font-family: 'Open Sans Semibold';
    font-size: 16px;
    line-height: 22px;
    color: $secondary;
  }

  &__descr {
    margin-bottom: 12px;
  }
}

.garantees {
  &__item {
    display: flex;
    justify-content: space-between;

    &:not(:last-child) {
      margin-bottom: 13px;

      @include small-tablet {
        margin-bottom: 15px;
      }

      @include mobile {
        margin-bottom: 8px;
        padding-right: 10px;
      }
    }
  }

  &__descr {}

  &__number {
    font-family: 'Open Sans Semibold';
    font-size: 16px;
    line-height: 180%;
    color: $secondary;
    margin-right: 12px;
  }
}

.warranty {
  display: flex;
  align-items: center;
  justify-content: flex-start;

  &__image {
    margin-right: 32px;

    @include mobile {
      margin-right: 16px;
    }
  }

  &__img {
    @include mobile {
      width: 36px;
      height: 54px;
    }
  }


  &__descr {
    padding-right: 50px;
    font-family: 'Open Sans';
    font-size: 24px;
    line-height: 150%;
    color: $black;

    @include small-tablet {
      padding-right: 25px;
    }

    @include mobile {
      font-size: 16px;
      line-height: 150%;
    }
  }
}
