.catalog-grid {
  padding: 0;
  grid-column: 9 span;

  &__title {
    margin-top: 0;
    margin-bottom: 20px;
    font-family: 'Open Sans';
    font-size: 24px;
    line-height: 130%;
    color: $black;

    @include tablet {
      display: none;
    }
  }

  &__list {
    margin-bottom: 30px;

    @include tablet {
      margin-bottom: 34px;
    }
  }

  &__item {
    grid-column: 4 span;

    @include tablet {
      grid-column: 3 span;
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 80px;

    @include small-tablet{
      min-width: 128px;
    }
  }

  &__btn {
    padding: 8px 11px;
    font-size: 16px;
    line-height: 100%;
    color: $primary;
    background-color: transparent;
    width: 32px;
    height: 32px;
    border: 2px solid $primary;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;




  }

  &__active {
    background-color: $primary_shade;
    border-color: $primary_shade;
    color: $white;
  }
}

.catalog-btn {
  &--3 {
    display: none;

    @include small-tablet {
      display: flex;
    }
  }
}
