@import 'vars';

.header {
  position: relative;

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    content: '';
    width: 100%;
    height: 53px;
    background-color: $grey_light;

    @include tablet {
      top: 58px;

    }

    @include mobile {
      top: 0;
    }


  }

  &__top {

    padding-top: 3px;

    @include tablet {
      padding-top: 6px;

    }

  }

  &__container {
    @include flex-column-start;

  }

  &__top-container {
    @include tablet {
      gap: 25px 32px;
    }

    @include small-tablet {
      gap: 25px 32px;
    }
  }

  &__top-left {
    grid-column: 5 span;
    display: flex;
    align-items: center;
    z-index: 10;

    @include tablet {
      grid-column: 9 span;
      background-color: none;
      justify-content: space-between;
    }

    @include small-tablet {
      grid-column: 6 span;
    }

    @include mobile {
      position: absolute;
      top: 58px;
      left: 12px;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }
  }

  &__top-right {

    display: flex;
    align-items: center;
    justify-content: flex-end;
    grid-column: 7 span;

    @include tablet {
      grid-column: 7 span;
      justify-content: space-between;

      &::before {
        top: 58px;
        width: 100vw;
        background-color: $grey_light;
      }
    }

    @include small-tablet {
      grid-column: 6 span;
    }

    @include mobile {
      padding: 16px 12px;
      padding-bottom: 9px;
      width: 100%;
      position: absolute;
      top: 573px;
      left: 0;
      z-index: 1000;
      background-color: $grey_light;
      transform: translateX(-100%);
      transition: transform 0.4s ease-in-out;
      background-image: none;

      &--active {
        z-index: 1001;
        transform: none
      }
    }
  }

  &__top-left-wrapper {
    @include flex-all-sb;
    margin-right: 26px;

    @include mobile {
      margin-bottom: 4px;
    }
  }

  &__top-left-descr {
    font-family: 'Open Sans';
    font-size: 16px;
    line-height: 120%;
    color: $black;
  }

  &__call-icon {
    margin-right: 5px;
    width: 17px;
    height: 17px;

    @include small-tablet {
      margin-right: 0px;
      width: 21px;
      height: 21px;
    }

    @include mobile {
      margin-right: 0px;
    }
  }

  &__basket{
    width: 28px;
  }
  use.call {
    transition: fill 0.4s ease-in-out;
    fill: $secondary;

  }

  &__call {
    display: flex;

    &--ef {
      position: relative;
      background-color: transparent;
      border: 0;
      z-index: 1;
      cursor: pointer;
      align-items: center;

      .header__call-link{
        transition: color 0.4s ease-in-out;
      }

      &::before {
        content: "";
        position: absolute;
        top: -2px;
        bottom: -2px;
        left: -2px;
        right: -2px;
        background-color: $bone;
        z-index: -1;
        opacity: 0;
        transition: opacity 0.4s ease-in-out;
      }

      @include for-desktop {
        &:hover:not(:focus-visible) use.call {
          fill: $secondary_light;
        }


        &:focus-visible::before {
          opacity: 1;
        }

        &:hover:not(:focus-visible) {
          .header__call-link {
            color: $secondary_light;
          }

        }
      }

      &:active:not(:focus-visible) use.call {
        fill: $secondary_shade;
      }

      &:active:not(:focus-visible) {
        .header__call-link {
          color: $secondary_shade;
        }

      }
    }

    @include tablet {}
  }

  &__call-link {
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 100%;
    color: $secondary;

  }

  &__list {
    @include flex-all-sb;
    width: 97%;


    @include tablet {
      width: 100%;
      justify-content: flex-start;
    }

    @include small-tablet {
      margin-right: 0;
    }

    @include mobile {

      display: flex;
      flex-direction: row;
      align-items: flex-start;
      flex-wrap: wrap;
    }
  }

  &__list-item {

    &:not(:last-child) {
      margin-right: 10px;
    }

    @include tablet {
      flex: 1 0 auto;
      &:not(:last-child) {
        margin-right: 27px;
      }
    }

    @include small-tablet {
      flex: 1 1 auto;
      &:not(:last-child) {
        margin-right: 10px;
      }
    }

    @include mobile {
      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 12px;
      }
      &:last-child{
        width: 41%;
        margin-right: 0;

      }
    }

    @include mobile {
      width: 50%;

    }
  }

  &__list-link {
    position: relative;
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 100%;
    color: $grey;

    &--ef {
      background-color: transparent;
      border: 0;
      z-index: 1;
      cursor: pointer;
      transition: color 0.4s ease-in-out;

      &::before {
        content: "";
        position: absolute;
        top: -2px;
        bottom: -2px;
        left: -5px;
        right: -5px;
        background-color: $pink_violet;
        z-index: -1;

        opacity: 0;
        transition: opacity 0.4s ease-in-out;
      }

      @include for-desktop {
        &:focus-visible::before {
          opacity: 1;

        }

        &:focus-visible {
          color: $primary-shade;
        }

        &:hover:not(:focus-visible) {
          color: $primary;
        }

      }

      &:active:not(:focus-visible) {
        color: $primary_shade;
      }
    }
  }

  &__average {
    position: relative;
    padding-top: 35px;
    padding-bottom: 29px;
    align-items: center;

    @include tablet {
      padding-top: 45px;
    }

    @include small-tablet {
      padding-top: 50px;
      padding-bottom: 24px;
    }

    @include mobile {
      padding-top: 150px;
      padding-bottom: 29px;
    }
  }

  &__average-left {
    display: flex;
    justify-content: space-between;
    grid-column: 3 span;

    @include tablet {
      grid-column: 2 span;
    }

    @include small-tablet {
      grid-column: 4 span;

    }

    @include mobile {
      grid-column: 6 span;
    }
  }


  &__lower {
    align-items: center;

    @include tablet {
      padding-bottom: 5px;
    }

    @include mobile {
      padding-bottom: 79px;
    }
  }

  &__lower-left {
    height: 60px;
    grid-column: 10 span;
    display: flex;

    @include tablet {
      grid-column: 9 span;
    }

    @include mobile {
      flex-direction: column;
      align-items: flex-start;
      grid-column: 6 span;
    }
  }

  &__lower-right {
    display: flex;
    justify-content: flex-end;
    grid-column: 2 span;

    @include tablet {
      position: absolute;
      top: 70px;
      right: 35px;
      width: 180px;
    }

    @include small-tablet {
      position: absolute;
      top: 140px;
      right: 71px;
    }

    @include mobile {
      top: 154px;
      right: 11px;
    }
  }

  &__btns {
    display: flex;
    padding: 0;
    width: 54%;
    justify-content: space-between;
    @include tablet {
      width: 54%;
      justify-content: space-between;
    }

    @include small-tablet {}

  }

  use.btns {
    transition: fill 0.4s ease-in-out;
    fill: $primary;
  }

  &__btn-login {


    &--ef {
      position: relative;
      background-color: transparent;
      border: 0;
      z-index: 1;
      cursor: pointer;
      transition:fill 0.4s ease-in-out;

      &::before {
        content: "";
        position: absolute;
        top: -4px;
        bottom: -4px;
        left: -4px;
        right: -4px;
        background-color: $primary_light;
        z-index: -1;
        opacity: 0;
        transition: opacity 0.4s ease-in-out;
      }

      @include for-desktop {
        &:hover:not(:focus-visible) use.btns {
          fill: $primary_light;
        }

        &:focus-visible use.btns {
          fill: $white;
        }

        &:focus-visible::before {
          opacity: 1;
        }
      }

      &:active:not(:focus-visible) use.btns {
        fill: $primary_shade;
      }

    }
  }




  use.search {
    fill: transparent;
    stroke: $primary;
  }

  &__search-wrapper {
    position: relative;

    width: 72%;

    @include tablet {
      width: 69%;
    }

    @include small-tablet {
      width: 73%;
    }

    @include mobile {
      width: 100%;
      margin-bottom: 16px;
    }
  }

  &__search-form {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: $grey_light;
    border: 2px solid $wild_sand;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;

    @include mobile {
      border-radius: 10px;
      height: 60px;
    }

    svg {
      position: absolute;
      top: 17px;
      left: 30px;
    }

    &--ef {
      transition: border-color 0.4s ease-in-out, background-color 0.4s ease-in-out, border 0.4s ease-in-out;

      @include for-desktop {
        &:hover:not(:focus-visible) {

          border-color: $md_grey;
          background-color: $md_grey;

        }

        &:focus-visible {
          border: 2px solid $primary;
          border-top-left-radius: 10px;
          border-bottom-left-radius: 10px;


        }

      }

      &:active:not(:focus-visible) {
        border: none;

        svg {
          fill: $primary;
        }
      }
    }
  }

  &__search-form-input {
    padding-left: 57px;
    background-color: $grey_light;
    background: none;
    width: 100%;
    height: 100%;
    color: $black;


  }

  &__search-arrow {
    position: absolute;
    top: 12px;
    right: 34px;
    width: 34px;
    height: 34px;

    @include tablet {
      top: 13px;
      right: 29px;
    }

    @include small-tablet {
      right: 30px;
    }

    @include mobile {}
  }

}

.personal-area-icon {
  width: 26px;
  height: 28px;
}

:-webkit-input-placeholder {
  transition: background-color 0.4s ease-in-out;
}

.burger {
  @include burger;
  display: none;

  @include small-tablet {
    display: block;

  }

  @include mobile {
    margin-right: 0;
  }
}

.logo {
  @include small-tablet {}

  @include mobile {
    position: absolute;
    top: 5px;
    left: 50%;
    transform: translateX(-50%);
  }
}
