.swiper-btns {
  position: absolute;
  top: 3px;
  right: 7px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;

  @include tablet {
    top: -1px;
    right: 22px;
  }

  @include small-tablet {
    right: 60px;
  }

  @include mobile {
    left: 52%;
    transform: translateX(-50%);
    top: 95%;
  }

  &__arrow {
    width: 32px;
    height: 32px;

    &:first-child {
      margin-right: 13px;

    }
  }

  &--cart {
    top: 0;
    right: -13px;

    @include mobile {
      top: 105%;
      left: 52%;
      transform: translateX(-50%);
    }
  }


  &__btn {

    .swiper-button-disabled {}

    svg {
      border-radius: 100%;
      transition: background-color 0.4s ease-in-out;
      path{
        transition: stroke 0.4s ease-in-out;
      }
      circle{
        transition: stroke 0.4s ease-in-out;
      }
    }

    @include for-desktop {
      &:focus-visible {
        svg {

          background-color: $primary_light;

          path {
            stroke: $white;
          }

          circle {

            stroke: $primary;
          }
        }
      }

      &:hover:not(:focus-visible) {
        svg {

          background-color: $primary;

          path {
            stroke: $white;
          }

          circle {

            stroke: $primary;
          }
        }
      }

    }

    &:active:not(:focus-visible) {
      svg {

        background-color: $primary_shade;

        path {
          stroke: $white;
        }

        circle {

          stroke: $primary_shade;
        }
      }
    }

  }

}

.scale-modal {

  &__prev {}

  &__next {}

  &__btn {

    display: none;

    @include tablet {
      display: block;
    }

    .swiper-button-disabled {}

    svg {
      border-radius: 100%;
      transition: background-color 0.4s ease-in-out;
      path{
        transition: stroke 0.4s ease-in-out;
      }
      circle{
        transition: stroke 0.4s ease-in-out;
      }
    }

    @include for-desktop {
      &:focus-visible {
        svg {

          background-color: $primary_light;

          path {
            stroke: $white;
          }

          circle {

            stroke: $primary;
          }
        }
      }

      &:hover:not(:focus-visible) {
        svg {

          background-color: $primary;

          path {
            stroke: $white;
          }

          circle {

            stroke: $primary;
          }
        }
      }

    }

    &:active:not(:focus-visible) {
      svg {

        background-color: $primary_shade;

        path {
          stroke: $white;
        }

        circle {

          stroke: $primary_shade;
        }
      }
    }

  }
}

.swiper-button-disabled {
  pointer-events: none;

  svg {
    path {
      fill: transparent;
      stroke: $grey;
    }

    circle {
      stroke: $grey;
    }

  }
}
