.categoria {
  background: $grey_light;
  border-radius: 10px;
  padding: 30px;

  @include small-tablet {
    padding: 28px;
  }

  @include mobile {
    position: relative;
    padding-bottom: 5px;
  }

  &--sm {
    @include mobile {
      padding: 12px 15px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 10px;

    &--sm {
      @include mobile {
        flex-direction: column;
        margin-bottom: 5px;
      }
    }

  }

  &__list-item {
    &:not(:last-child) {
      margin-right: 30px;

      @include mobile {
        margin-right: 20px;
      }
    }

    &--sm {
      @include mobile {
        margin-bottom: -5px;
      }
    }

  }


  &__link {
    position: relative;
    cursor: pointer;
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 100%;
    color: $grey;

    &--sm {
      @include mobile {
        font-size: 10px;
      }
    }

    &--ef {
      background-color: transparent;
      border: 0;
      z-index: 1;
      cursor: pointer;
      transition: opacity 0.4s ease-in-out, color 0.4s ease-in-out;

      &::before {
        content: "";
        position: absolute;
        top: -2px;
        bottom: -2px;
        left: -5px;
        right: -5px;
        background-color: $pink_violet;
        z-index: -1;

        opacity: 0;
        transition: opacity 0.4s ease-in-out;
      }

      @include for-desktop {
        &:focus-visible::before {
          opacity: 1;
        }

        &:hover:not(:focus-visible) {
          color: $primary;
        }

      }

      &:active:not(:focus-visible) {
        color: $primary-shade;
      }
    }
  }

  &__title {
    font-family: 'Open Sans';
    font-size: 24px;
    line-height: 33px;
    color: $black;
    margin-bottom: 30px;

    @include tablet {
      margin-bottom: 31px;
    }

    @include small-tablet {
      margin-bottom: 30px;
    }

    @include mobile {
      margin-bottom: 13px;
    }

    &--sm {
      @include mobile {
        font-size: 16px;
      }
    }
  }

  &__image {
    display: flex;
    min-height: 202px;
    justify-content: flex-start;
    align-items: flex-start;
    padding-left: 0;
    margin-bottom: 32px;
    transform: translateX(-30px);

    @include tablet {
      margin-bottom: 30px;
    }

    @include mobile {

      margin-bottom: 0px;
    }

    img {
      display: block;

      @include tablet {
        min-height: 296px;

        transform: translateX(0px);
      }

      @include mobile {
        min-height: 203px;
        margin-bottom: 10px;
      }
    }

  }

  &__btn-link {

    font-family: 'Open Sans Bold';
    font-size: 16px;
    line-height: 120%;
    color: $primary;
    transition: color 0.4s ease-in-out;

    @include mobile {
      display: none;
    }
  }

  svg {
    border-radius: 100%;
    transform: translateX(5px);
    transition: all 0.4s ease-in-out;
  }

  &__wrapper {
    display: flex;
  }

  &__btn {
    position: relative;
    background: transparent;
    display: flex;
    cursor: pointer;
    transition: opacity 0.4s ease-in-out, background-color 0.4s ease-in-out, color 0.4s ease-in-out, stroke 0.4s ease-in-out  ;

    svg{
      transition: background-color 0.4s ease-in-out;
      path{
        transition: stroke 0.4s ease-in-out;
      }
      circle{
        transition: stroke 0.4s ease-in-out;
      }
    }

    @include mobile {
      display: none;
    }

    &--wrapper {
      display: none;

      @include mobile {
        display: block;

        svg {

          transform: translate(6px, -3px);
        }
      }
    }

    &--sm {

      @include mobile {
        display: none;

        svg {
          transform: translate(5px, -6px);
        }
      }
    }

    &::before {
      content: '';
      position: absolute;
      background-color: $primary;
      bottom: 2px;
      left: 0;
      height: 2px;
      width: 82px;
      opacity: 0;
      transition: opacity 0.4s ease-in-out;
    }

    &--ef {
      @include for-desktop {
        &:hover:not(:focus-visible) {
          &::before {
            opacity: 1;
          }

          svg {
            background-color: $primary;
          }

          svg path {
            stroke: $white;

          }
        }

        &:focus-visible {
          &::before {
            opacity: 1;
          }

          svg {
            background-color: $primary_light;
          }

          svg path {
            stroke: $white;

          }
        }

      }

      &:active:not(:focus-visible) {
        .categoria__btn-link {
          color: $primary-shade;
        }

        &::before {
          background-color: $primary-shade;
          opacity: 1;
        }

        svg {
          background-color: $primary_shade;
        }

        svg path {
          stroke: $white;
        }

        svg circle {
          stroke: $primary_shade;
        }
      }

    }


  }
}
