.catalog {
  &__container {
    @include mobile {
      padding-top: 30px;
    }
  }

  &__content {
    padding-bottom: 70px;

  }

  &__title {
    @include tablet {
      font-family: 'Open Sans';
      position: inherit;
      display: block;
      overflow: visible;
      font-size: 24px;
      line-height: 130%;
      margin-bottom: 17px;
    }

    @include mobile {
      margin-bottom: 4px;
    }
  }

  &__list {
    @include tablet {

      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      flex-wrap: wrap;
      grid-column: 8 span;
    }

    @include small-tablet {
      justify-content: flex-start;
      grid-column: 6 span;
    }

    @include mobile {
      width: 80%;
    }

    @include small-mobile {
      width: 100%;
    }

  }

  &__list-item {
    @include tablet {

      grid-column: 2 span;

    }

    @include small-tablet {
      &:nth-child(3) {
        margin-bottom: 0;
      }

      &:nth-child(4) {
        margin-bottom: 0;
      }
    }
  }
}
