.collection {

  @include bg-cover;
  background-image: url('../img/oxford.png');

  @include tablet {
    background-image: url('../img/oxford_1024.png');
    min-height: 599px;

  }

  @include small-tablet {
    background-image: url('../img/oxford_768.png');
  }

  @include small-tablet {
    padding: 0 36px;
  }

  @include mobile {
    padding: 0;
    padding-bottom: 91px;
    position: relative;
    background-image: none;
    min-height: 302px;
  }

  &__container {

    @include small-tablet {
      padding: 0;
    }

    @include mobile {
      padding: 0 12px;
      background-image: url('../img/oxford_320.png');
      background-repeat: no-repeat;
      background-size: cover;
      min-height: 210px;
      padding-top: 32px;
      min-height: 210px;

    }
  }

  &__cart {
    padding: 151px 0;
    @include small-tablet{
      padding-top: 154px;
    }
    @include mobile {

      padding: 0;
    }
  }

  &__title {
    font-family: 'Playfair Display';
    font-size: 60px;
    line-height: 120%;
    color: $gold;

    @include mobile {
      font-size: 24px;
    }
  }

  &__descr {
    width: 39.68%;
    font-family: 'Open Sans';
    font-size: 48px;
    line-height: 120%;
    color: $white;
    margin-bottom: 52px;

    @include tablet {
      width: 50.68%;
    }

    @include small-tablet {
      width: 70.68%;
      margin-bottom: 51px;
    }

    @include mobile {
      font-size: 16px;
    }
  }

}
