.input.just-validate-error-field:focus-visible{
  border: 1px solid $red_input !important;
}
.just-validate-error-field{
  border: 1px solid $red_input;
}

.just-validate-success-field{
  border: 1px solid $green_input !important;
}

.tooltip {
  position: relative;

  &__btn {
    position: absolute;
    top: 2px;
    left: 2px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    width: 14px;
    height: 14px;

    svg{
      width: 14px;
      height: 14px;
    }
    &:hover{
      .tooltip-1{
        display: none;
      }
      .tooltip-2{
        display: block;
      }
    }
  }

  &__txt{
    position: absolute;
    left: -70px;
    bottom: calc(100% + 13px);
    z-index: 3;
    padding: 5px;
    width: max-content;
    max-width: 157px;
    font-family: 'Open Sans';
    font-size: 9px;
    line-height: 14px;
    color: $white;
    background: $black;
    opacity: 0;
    text-align: center;
    visibility: hidden;
    transform: translateY(10px);
    transition: opacity 0.4s ease-in-out,transform 0.4s ease-in-out,visibility 0.4s ease-in-out;

    @include small-tablet{
      max-width: 140px;
    }
  }
  &__txt::after{
    content: '';
    position: absolute;
    left: 70px;
    bottom: -7px;
    display: inline-block;
    width: 0;
    height: 0;
    border-width: 8px 8.5px 0 8.5px;
    border-style: solid;
    border-color: $black transparent transparent transparent;
  }
}
.tooltip-1{
  display: block;
}
.tooltip-2{
  display: none;
}

.tooltip__btn:hover + .tooltip__txt {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.tooltip__btn:focus + .tooltip__txt {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.just-validate-error-label{
  font-family: 'Open Sans';
  font-size: 12px;
  line-height: 16px;
  padding-left: 18px;
  color: $red_input !important;
}

