/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.noUi-target {
  background-color: $md_grey;
  border: none;
  border-radius: 3px;
  box-shadow: none;
  height: 3px;
}

.noUi-connect {
  background-color: $primary;

}

.noUi-handle::after,
.noUi-handle::before {
  display: none;
}

.noUi-handle {
  border: none;
  box-shadow: none;
  width: 13px;
  height: 13px;

}

.noUi-horizontal .noUi-handle {
  right: -6px;
  top: 9px;
  width: 13px;
  height: 13px;
}

.noUi-touch-area {
  position: relative;
  width: 13px;
  height: 4px;
  background-color: $primary;
  border-radius: 13px;
  transition: background-color 0.4s ease-in-out;

  &::before {
    position: absolute;
    top: -5px;
    left: -2px;
    transform: rotate(-60deg);
    content: '';
    width: 13px;
    height: 6px;
    background-color: $primary;
    border-radius: 13px;
    transition: background-color 0.4s ease-in-out;
  }

  &::after {
    position: absolute;
    top: -5px;
    right: -2px;
    transform: rotate(60deg);
    content: '';
    width: 13px;
    height: 6px;
    background-color: $primary;
    border-radius: 13px;
    transition: background-color 0.4s ease-in-out;
  }


}

.noUi-handle-lower {
  @include for-desktop {
    &:focus-visible {
      .noUi-touch-area {
        background-color: $primary_shade;

        &::before {
          background-color: $primary_shade;
        }

        &::after {
          background-color: $primary_shade;
        }
      }

      .noUi-connect {
        background-color: $primary_shade;
      }
    }

    &:hover:not(:focus-visible) {
      .noUi-touch-area {
        background-color: $primary_light;

        &::before {
          background-color: $primary_light;
        }

        &::after {
          background-color: $primary_light;
        }
      }
    }

  }

  &:active:not(:focus-visible) {
    .noUi-touch-area {
      background-color: $primary_shade;

      &::before {
        background-color: $primary_shade;
      }

      &::after {
        background-color: $primary_shade;
      }
    }

    .noUi-connect {
      background-color: $primary_shade;
    }
  }
}


