.cart-product {
  grid-column: 12 span;
  grid-template-rows: 230px 80px;
  gap: 32px;

  margin-bottom: 32px;

  @include tablet {
    gap: 0 32px;
    grid-template-rows: 220px 80px;
  }

  @include small-tablet {
    grid-template-rows: 250px 347px;
    gap: 30px 32px;

  }

  @include mobile {
    grid-template-rows: 220px 81px 247px;
    margin-bottom: 20px;
    gap: 11px;

  }
  @include small-mobile {
    grid-template-rows: 121px 81px 247px;
    margin-bottom: 20px;
    gap: 11px;

  }


  &__image {
    grid-column: 6 span;

    img {
      width: 100%;
    }

    @include tablet {
      grid-column: 5 span;
    }

    @include small-tablet {
      grid-column: 6 span;
    }
  }


  &__slider {
    grid-column: 6 span;
    padding: 0 24px;
    background-color: $grey_light;
    overflow: hidden;

    @include tablet {
      grid-column: 5 span;
      padding: 0 10px;
    }

    @include small-tablet {
      grid-column: 2 span;
      padding: 22px;
      padding-bottom: 24px;

      .swiper-wrapper {
        align-items: center;
      }
    }

    @include mobile {
      grid-column: 6 span;
      padding: 0;
      padding-left: 10px;
    }
  }

  &__slide {
    display: flex;
    justify-content: center;
    align-items: center;

    @include mobile{
      justify-content: flex-start;
    }
    &:nth-child(1) {
      width: 21% !important;
      @include tablet{
        width: 25% !important;
      }
      @include small-tablet {
        width: 100% !important;
      }

      @include mobile {
        width: 40% !important;
      }
      @include small-mobile {
        width: 51% !important;
      }
    }

    &:nth-child(2) {
      width: 12% !important;

      @include tablet {
        width: 13% !important;
      }

      @include small-tablet {
        width: 100% !important;
      }

      @include mobile {
        width: 25% !important;
      }
      @include small-mobile {
        width: 31% !important;
      }
    }

    &:nth-child(3) {
      width: 24% !important;

      @include tablet {
        width: 27% !important;
      }

      @include small-tablet {
        width: 100% !important;
      }

      @include mobile {
        width: 37% !important;
      }
      @include small-mobile {
        width: 50% !important;
      }
    }

    &:nth-child(4) {
      @include tablet {
        width: 30% !important;
      }

      @include small-tablet {
        width: 100% !important;
      }

      @include mobile {
        width: 50% !important;
      }
    }

  }

  &__wrapper {
    grid-column: 6 span;
    padding-top: 14px;

    @include tablet {
      grid-column: 6/10;
      grid-row-start: 1;
      grid-row-end: 3;
      order: 2;
    }

    @include small-tablet {
      grid-column: 3/7;
      grid-row-start: 2;
      grid-row-end: 3;
      padding-top: 48px;
      padding-bottom: 38px;
      order: 3;
    }

    @include mobile {
      grid-column: 6 span;
      grid-row-start: 3;
      grid-row-end: 4;
      padding-top: 6px;
      padding-bottom: 0px;

    }
  }

  &__rating {
    display: inline-flex;
    align-items: center;
    font-family: 'Open Sans';
    font-size: 16px;
    line-height: 130%;
    color: $secondary;

    svg {
      fill: currentColor;
      margin-right: 7px;
    }
  }

  &__title {
    // margin-bottom: 5px;
    font-family: 'Open Sans';
    font-size: 24px;
    line-height: 130%;
    color: $black;

    @include mobile {
      font-size: 16px;
      line-height: 130%;
    }
  }

  &__descr {
    margin-bottom: 27px;
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 100%;
    color: $grey;
    transform: translateY(6px);

    @include mobile {
      margin-bottom: 25px;
      width: 80%;
      transform: translateY(9px);

    }
  }

  &__price {
    margin-bottom: 21px;
    font-family: 'Open Sans';
    font-size: 36px;
    line-height: 130%;
    color: $black;

    @include mobile {
      margin-bottom: 16px;
      font-size: 24px;
      line-height: 130%;
    }
  }

  &__btns {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

  }

  &__btn {
    padding: 19px 30px;

    @include mobile {
      width: 100%;
    }

    &--add {
      padding: 12px 33px;
      font-family: 'Open Sans';
      font-size: 16px;
      line-height: 130%;
      color: $primary;
    }
  }

}
