.basket{
    position:relative;

    &__btn{
        &--inactive{
            pointer-events: none;
        }
    }

    &--ef{
        position: relative;
        background-color: transparent;
        border: 0;
        z-index: 1;
        cursor: pointer;
        transition: fill 0.4s ease-in-out;

        &::before{
            content: "";
            position: absolute;
            top: -3px;
            bottom: -3px;
            left: -15px;
            right: -3px;
            background-color:$primary_light;
            z-index: -1;
            opacity: 0;
            transition: opacity 0.4s ease-in-out;
        }

        &:active:not(:focus-visible) use.btns{
            fill: $primary_shade;
        }

        @include for-desktop{
            &:hover:not(:focus-visible) use.btns{
                fill: $primary_light;
            }
            &:focus-visible use.btns{
                fill: $white;
            }

            &:focus-visible::before{
                opacity: 1;
            }
        }



    }

    &__icon{
        width: 28px;
        height: 28px;
        pointer-events: none;
    }
    &__count{
        position: absolute;
        top: 6px;
        left: -14px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        height: 16px;
        min-width: 16px;
        background-color: $secondary;
        font-family: 'Open Sans';
        font-size: 10px;
        line-height: 100%;
        color: $white;
        border-radius: 100%;
        opacity: 0;
        transition: opacity 0.4s ease-in-out;
        pointer-events: none;

        &--visible{
            opacity: 1;
            transition: opacity 0.4s ease-in-out;
        }
    }

    .basket-content-catalog{


    }
    .basket-content{
        position: absolute;
        right: 0;
        top: calc(100% + 22px);
        width: 490px;
        background-color: $white;
        border-radius: 10px;
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.4s ease-in-out , visibility 0.4s ease-in-out;

        @include mobile{
            width: 280px;
            right: 10px;
        }



        &--visible{
            opacity: 1;
            visibility: visible;
            transition: opacity 0.4s ease-in-out , visibility 0.4s ease-in-out;
        }

        &__list{
            max-height: 330px;


            &__item{
                z-index: 100;
            }
        }

        &__btn{
            @include mobile{
                padding: 15px 10px;
                font-size: 12px;
                line-height: 100%;
            }
        }

        .basket-product{
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 12px 20px;
            transition: bacground-color 0.4s ease-in-out;

            @include mobile{
                padding: 10px 10px;
            }

            &:hover{
                background-color: $md_grey;
            }

            &__img{
                max-width: 100px;
                max-height: 100px;
                object-fit: cover;

                @include mobile{
                    max-width: 50px;
                    max-height: 50px;
                }
            }
            &__text{
                .basket-product__title{
                    @include mobile{
                        font-size: 12px;
                        line-height: 100%;
                    }
                }

            }

            &__title{
                max-width: 280px;
                margin-bottom: 10px;
                font-family: 'Open Sans';
                font-size: 20px;
                line-height: 130%;
                color: $black;
                @include mobile{
                    max-width: 135px;
                    font-size: 14px;
                    line-height: 120%;
                }
            }

            &__price{
                font-family: 'Open Sans';
                font-size: 16px;
                line-height: 130%;
                color:  $grey;
                @include mobile{
                    font-size: 12px;
                    line-height: 120%;
                }
            }

            &__delete{
                width: 40px;
                height: 40px;
                border: none;
                background: transparent;
                padding: 0;
                background-image: url('../img/basket.svg');
                background-size: cover;
                background-position: center;
                background-repeat: no-repeat;
                cursor: pointer;
                @include mobile{
                    width: 30px;
                    height: 30px;
                }
            }

        }

        &__bottom{
            padding: 20px;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        &__fullprice{
            display: flex;
            flex-direction: column;
            font-family: 'Open Sans';
            font-size: 20px;
            line-height: 130%;
            color: $black;
            @include mobile{
                font-size: 14px;
            }
        }
    }



    .fullprice{
        font-family: 'Open Sans Bold';
        font-size: 20px;
        line-height: 130%;
        color: $black;
        @include mobile{
            font-size: 14px;
        }
    }
}
