.simplebar-track.simplebar-vertical{
    width: 4px;
    background-color: $primary_light;
    border-radius: 5px;
    margin-top: 20px;
    height: calc(100% - 40px);
    right: 5px;
}

.simplebar-track.simplebar-vertical .simplebar-scrollbar:before {
    background-color: $primary;
    border-radius: 5px;
    width: 100%;
    opacity: 1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}


