.contact {
  padding-bottom: 70px;

  &__container {
    @include mobile {
      padding-top: 30px;

    }
  }

  &__map {
    position: relative;

    .map {
      height: 520px;

      @include tablet {
        height: 510px;
      }
    }
  }

  &__form {

    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 32px;

    @include mobile {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__input {
    width: 624px;
    height: 54px;
    margin-right: 32px;
    background-color: $grey_light;
    border: 2px solid $wild_sand;
    border-radius: 10px;

    @include small-tablet {
      width: 65%;
      padding-left: 17px;
    }

    @include mobile {
      width: 100%;
      margin-right: 0px;
      margin-bottom: 15px;
    }

  }

  &__btn {
    padding: 19px 35px;
  }

  .ymaps-2-1-79-map {
    width: 100% !important;
    height: 100% !important;

  }

  .ymaps-2-1-79-search,
  .ymaps-2-1-79-listbox__button,
  .ymaps-2-1-79-traffic,
  .ymaps-2-1-79-listbox__button,
  .ymaps-2-1-79-controls__toolbar_right,
  .ymaps-2-1-79-controls__toolbar_left,
  .ymaps-2-1-79-zoom__scale,
  .ymaps-2-1-79-map-copyrights-promo,
  .ymaps-2-1-79-copyright__agreement,
  .ymaps-2-1-79-controls__control_toolbar .ymaps-2-1-79-user-selection-none,
  .ymaps-2-1-79-controls__control {
    display: none;
  }
}

.contact-card {

  max-width: 343px;
  height: 284px;
  position: absolute;
  top: 5%;
  right: 12%;
  border-radius: 20px;
  background-color: $white;

  @include mobile {
    max-width: 232px;
    height: 269px;
  }

  &__wrapper {
    padding: 30px;
    position: relative;

    @include mobile {
      padding: 24px 20px;
      padding-right: 15px;
    }
  }

  &__btn {
    position: absolute;
    top: 20px;
    right: 20px;

    @include mobile {
      top: 10px;
      right: 10px;
    }
  }

  &__title {
    margin-bottom: 7px;
    font-family: 'Open Sans Bold';
    font-size: 16px;
    line-height: 100%;
    color: $black;

    @include mobile {
      margin-bottom: 5px;
      font-size: 14px;
      line-height: 19px;
    }
  }

  &__adress {
    margin-bottom: 8px;
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 180%;
    color: $black;

    @include mobile {
      margin-bottom: 11px;
      font-size: 12px;
      line-height: 16px;
    }
  }

  &__item {
    &:not(:last-child) {
      @include mobile {
        margin-bottom: 26px;
      }
    }
  }

  &__call-icon {
    margin-right: 5px;
    width: 17px;
    height: 17px;

    @include mobile {
      margin-right: 0;
      width: 23px;
      height: 23px;
    }
  }

  use.call {
    transition: fill 0.4s ease-in-out;
    fill: $secondary;

  }

  &__call {
    display: flex;
    margin-bottom: 26px;
    align-items: center;

    @include mobile {
      margin-bottom: 23px;
    }

    &--ef {
      position: relative;
      background-color: transparent;
      border: 0;
      z-index: 1;
      cursor: pointer;
      transition:fill 0.4s ease-in-out, color 0.4s ease-in-out;

      &::before {
        content: "";
        position: absolute;
        top: -2px;
        bottom: -2px;
        left: -2px;
        right: -2px;
        background-color: $bone;
        z-index: -1;
        opacity: 0;
        transition: opacity 0.4s ease-in-out;
      }

      @include for-desktop {
        &:hover:not(:focus-visible) use.call {
          fill: $secondary_light;
        }


        &:focus-visible::before {
          opacity: 1;
        }

        &:hover:not(:focus-visible) {
          .header__call-link {
            color: $secondary_light;
          }

        }
      }

      &:active:not(:focus-visible) use.call {
        fill: $secondary_shade;
      }

      &:active:not(:focus-visible) {
        .header__call-link {
          color: $secondary_shade;
        }

      }
    }
  }

  &__call-link {
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 100%;
    color: $secondary;

  }

  &__prop {
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 180%;
    color: $grey;

    @include mobile {
      font-size: 12px;
      line-height: 140%;
    }
  }

  &__descr {
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 180%;
    color: $black;

    @include mobile {
      font-size: 12px;
      line-height: 140%
    }
  }

}
