.cart-midle {
  padding-bottom: 75px;

  @include tablet {
    padding-bottom: 65px;
  }

  @include small-tablet {
    padding-bottom: 26px;
    gap: 30px;
  }

  @include mobile {
    padding-bottom: 65px;
  }

  &__left {
    grid-column: 6 span;

    @include tablet {
      grid-column: 5 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__prop {
    grid-column: 6 span;

    @include tablet {
      grid-column: 4 span;
    }

    @include small-tablet {
      grid-column: 6 span;
    }
  }


  &__title {
    margin-bottom: 8px;
    font-family: 'Open Sans Semibold';
    font-size: 16px;
    line-height: 100%;
    color: $black;
  }

  &__descr {
    margin-bottom: 105px;
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 180%;
    color: $black;

    @include tablet {
      margin-bottom: 80px;
    }

    @include small-tablet {
      margin-bottom: 30px;
    }
  }

  &__wrap {
    display: inline-block;

    &--1 {
      margin-bottom: 8px;
    }
  }

  &__right {
    grid-column: 6 span;
  }
}

.scheme {
  display: flex;
  align-items: center;
  justify-content: flex-start;

  &__item {
    img {
      width: 100%;
    }

    &:not(:last-child) {
      margin-right: 32px;

      @include tablet {
        margin-right: 24px;
      }

      @include mobile {
        margin-right: 17px;
      }
    }
  }
}

.cart-prop {
  &__item {
    display: flex;
    justify-content: space-between;

    // border-bottom: 1px dotted $grey; /* .   ,     */
    @include mobile {
      flex-direction: column;

      &:not(:last-child) {
        margin-bottom: 5px;
      }
    }
  }

  &__list {
    @include small-tablet {
      transform: translateY(-6px);
    }

    @include mobile {
      transform: translateY(-2px);
    }
  }

  &__props {
    position: relative;
    align-items: baseline;
    width: 53%;
    display: flex;
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 180%;
    color: $grey;



    &::before {
      position: absolute;
      bottom: 7px;
      left: 0;
      margin: 0 4px;
      content: '';
      height: 10px;
      width: 100%;
      background-image: url('../img/dott.svg');
      background-size: 4px 2px;
      background-repeat: repeat-x;
      background-position: 0 bottom;
      display: block;
      z-index: -1;

      @include mobile {
        display: none;

      }
    }

    @include tablet {
      width: 46%;
    }

    @include small-tablet {
      width: 65%;
    }

    @include mobile {
      font-size: 10px;
      line-height: 14px;
      width: 100%;
    }
  }
  &__text{
    background-color: $white;
    z-index: 2;
  }
  &__values {
    width: 47%;
    text-align: left;
    position: relative;
    font-family: 'Open Sans';
    font-size: 14px;
    line-height: 180%;
    color: $black;
    order: 3;

    @include tablet {
      width: 54%;
    }

    @include small-tablet {
      width: 35%;
    }

    @include mobile {
      width: 100%;
      line-height: 22px;
    }
  }
}

