.hero {
  padding-top: 29px;

  &__swiper {
    width: 100%;

  }

  &__cart {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 82px;
    padding-left: 60px;
    height: 100%;

    @include tablet {
      padding-top: 78px;
      padding-left: 37px;
    }

    @include small-tablet {
      padding-left: 73px;
    }

    @include mobile {
      padding-top: 30px;
      padding-left: 30px;
    }
  }

  &__btn {
    @include mobile {
      display: block;
      width: 196px;
      position: absolute;
      bottom: -62px;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 48px;
    font-family: 'Open Sans Bold';
    font-size: 48px;
    line-height: 65px;
    color: $grey;
    max-width: 39%;

    @include tablet {
      max-width: 55%;
      margin-bottom: 50px;
    }

    @include small-tablet {
      max-width: 80%;
      margin-bottom: 50px;
    }

    @include mobile {
      font-size: 20px;
      line-height: 27px;
    }

    &--light {
      max-width: 45%;
      color: $white;

      @include tablet {
        max-width: 60%;
        margin-bottom: 50px;
      }

      @include small-tablet {
        max-width: 90%;
      }
    }
  }
}
