.categories {
  padding-top: 70px;
  padding-bottom: 65px;

  @include small-tablet{
    padding-bottom: 70px;
  }
  @include mobile {
    padding-bottom: 79px;
  }

  &__list {
    gap: 32px;

    @include tablet {
      gap: 27px;
    }

    @include small-tablet {
      gap: 34px;
    }

    @include mobile {
      gap: 32px 15px;
    }
  }

  &__list-item {
    grid-column: 4 span;
    overflow: hidden;

    @include tablet {

      grid-column: 3 span;

      &:not(:first-child) .categoria__image {
        margin-bottom: 40px;

        img {
          @include mobile {
            min-height: 105px;
          }
        }

      }

      &:not(:first-child) .categoria__title {
        margin-bottom: 35px;

        @include mobile {
          margin-bottom: 9px;
        }
      }
    }

    @include mobile {}

    &--1 {
      @include tablet {
        order: 0;
      }

      @include mobile {
        height: 332px;
      }
    }

    &--2 {
      @include tablet {
        order: 3;

        .categoria__image {
          transform: translateX(-40px);
        }
      }

      @include small-tablet {
        order: 2;
      }

      @include mobile {
        height: 206px;

        .categoria__image {
          img {
            transform: translateX(25px);
          }
        }
      }
    }

    &--3 {

      @include tablet {
        order: 1;
      }

      @include small-tablet {
        order: 1;
      }

      @include mobile {
        height: 206px;

        .categoria__image {
          img {
            transform: translateX(15px);
          }
        }
      }
    }

    &--4 {

      @include tablet {
        order: 2;
      }

      @include small-tablet {
        order: 3;
      }

      @include mobile {
        height: 206px;

        .categoria__list {
          margin-bottom: 11px;
        }

        .categoria__title {
          line-height: 104%;
          margin-bottom: 21px !important;
        }

        .categoria__image {
          min-height: 86px !important;

          img {
            min-height: 86px !important;
            transform: translateX(15px);
          }
        }
      }
    }

    &--5 {

      @include tablet {
        order: 4;

        .categoria__image {
          transform: translateX(-143px);
        }
      }

      @include small-tablet {
        order: 4;
      }

      @include mobile {
        height: 206px;

        .categoria__image {

          img {

            transform: translateX(128px);
          }
        }
      }
    }


    &--bg {
      grid-column: 6 span;
    }

    &--tb {
      @include tablet {
        grid-column: 3 span;
      }
    }
  }

}
